import React from 'react';
import { reduxForm } from 'redux-form';
import { maxLengthCreator, required } from '../../../Utils/validator/validator';
import { createField, Input, Textarea } from '../../coummon/Prilouder/formControls/formControls';
import classes from './ProfileInfo.module.css'

const ProfileDataForm = (props) => {

    const profile = props.profile

    let socialContacts = [];

    for (let contact in profile.contacts) {
        socialContacts.push(<div><span className={classes.item}>{contact}</span>: {profile.contacts[contact]}</div>)
    };

    return (
        <form onSubmit={props.handleSubmit}>
            {/* {props.isOuner && <button onClick={props.editFormInfo} className={classes.editButton}>Редактировать</button>} */}
            <div>
                <span className={classes.item}>полное имя:{createField("Полное имя", "fullName", Input, [])}</span>
            </div>
            <div>
                <div><span className={classes.item}>Обо мне: </span>{createField("о себе", "aboutMe", Textarea, [])}</div>
            </div>
            <div>
                <div><span>ищю работу:</span>{createField("", "lookingForAJob", Input, [], { type: 'checkbox' })}</div>
            </div>
            <div>
                <div><span>что умеете:</span>{createField("профессиянальные навыки", "lookingForAJobDescription", Textarea, [])}</div>
            </div>
            <div>
                <hr />
                <div><span className={classes.contactsHead}>Контакты:</span></div>
                {socialContacts}
            </div>
            <button >Сохранить</button>
        </form>
    )
};

const WithReduxForm = reduxForm({ form: "edit-profile" })(ProfileDataForm);


export default WithReduxForm;
