const ADD_MESSAGE = 'ADD_MESSAGE';

let initial = {
  messagesList: [
      {id: '1', dialogname: 'имя', img: null},
      {id: '2', dialogname: 'имя', img: ""},
      {id: '3', dialogname: 'имя', img: null},
      {id: '4', dialogname: 'имя', img: null},
      {id: '5', dialogname: 'имя', img: null}
  ],
  dialog: [
      {id: '0', message: 'сообщение'},
      {id: '1', message: 'сообщение'},

  ]
}

let messagesReducer = (state = initial, action) => {
    switch (action.type) {
        case ADD_MESSAGE:
          return {
            ...state,
            dialog: [...state.dialog, {id: '4', message: action.messageBody}]
          }
        default:
            return state;
    }
};

export let addMessageAC = (messageBody) => {
  return {type: ADD_MESSAGE, messageBody}
}
 
export default messagesReducer;

